:root {
    --main-bg: #ffffff;
    --second-bg: #fafafb;
    --txt-color: #455560;
    --txt-white: #fff;
    --main-color: #349eff;
    --second-color: #bf62ff;
    /* --box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px; */
    --box-shadow: 13px 13px 20px #cbced1,
    -13px -13px 20px #ffffff;

    --main-bg-light: #ffffff;
    --second-bg-light: #fafafb;
    --txt-color-light: #455560;
    --box-shadow-light: rgba(149, 157, 165, 0.2) 0px 8px 24px;

    --main-bg-dark: #2d2d2d;
    --second-bg-dark: #202020;
    --txt-color-dark: #bbbbbb;
    --box-shadow-dark: rgba(0, 0, 0, 0.2) 0px 5px 10px;
    

    --main-color-blue: #349eff;
    --second-color-blue: #62b4ff;

    --main-color-red: #fb0b12;
    --second-color-red: #ff4a6b;

    --main-color-cyan: #10d4d2;
    --second-color-cyan: #2ae9e6;

    --main-color-green: #019707;
    --second-color-green: #4caf50;

    --main-color-orange: #d68102;
    --second-color-orange: #fca11a;
    --box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;

    --sidebar-width: 300px;
    --border-radius: 15px;
    --headernav-height: 90px;
    --footer-height: 90px;
    --transition-cubic: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}

body {
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    letter-spacing: 0.7px;
    overflow-x: hidden;

}

a {
    text-decoration: none;
    color: unset;
}

a:hover {
    color: #349eff;
    color: var(--main-color);
}

button {
    cursor: pointer;
    border: 0;
    outline: 0;
    color: inherit;
}




ul {
    list-style-type: none;
}

input {
    border: 2px solid transparent;
    outline: 0;
}



.page-header {
    margin: 30px 0;
    padding-left: 40px;
    text-transform: capitalize;
}
.status__card_container{
    padding: 0 20px;
}

.card {
    padding: 30px;
    margin-bottom: 10px;
    border-radius: 15px;
    border-radius: var(--border-radius);
}

.full-height {
    height: 100%;
}

.card.full-height {
    height: calc(100% - 30px);
}

.card__header {
    text-transform: capitalize;
}

.card > div ~ div {
    margin-top: 30px;
}

.card__footer {
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;
}

.light-background {
    background-color: #ffffff;
    background-color: var(--main-bg-light);
    color: #000;
}

.dark-background {
    background-color: #2d2d2d;
    background-color: var(--main-bg-dark);
    color: #fff;
}

.blue-color {
    background-color: #349eff;
    background-color: var(--main-color-blue);
    color: #fff;
    
}

.red-color {
    background-color: #fb0b12;
    background-color: var(--main-color-red);
    color: #fff;
}

.cyan-color {
    background-color: #10d4d2;
    background-color: var(--main-color-cyan);
    color: #fff;
}

.green-color {
    background-color: #019707;
    background-color: var(--main-color-green);
    color: #fff;
}

.orange-color {
    background-color: #d68102;
    background-color: var(--main-color-orange);
    color: #fff;
}


.content__wapper{
    margin: 30px 0;
}

.theme-mode-dark{
    background-color: #2d2d2d;
    background-color: var(--main-bg-dark);
    color: #fff;
}



.custom_btn{
    padding: 5px 10px;
    font-size: 18px;
    text-transform: capitalize;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    color: #fff;
}
.custom_btn:hover{
    box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;
    box-shadow: var(--box-shadow);
    transform: scale(1.05);
    -webkit-transform: scale(1.05);
    -moz-transform: scale(1.05);
    -ms-transform: scale(1.05);
    -o-transform: scale(1.05);
}

.btn__primary{
    background-color: #349eff;
    background-color: var(--main-color-blue);
}

.btn__info{
    background-color: #3cbfd5;
}


@media(max-width: 480px){
    .card {
        padding: 30px 0;
    }
}
.sidebar{
    min-width: var(--sidebar-width);
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: var(--box-shadow);
    background-color: var(--main-bg);
}

.sidebar__logo{
    min-width: var(--sidebar-width);
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding-left: 30px;
}

.sidebar__logo > img{
    height: 100px;
    width: 90px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
