    color: var(--txt-white);
}

.header__nav__search > i {
    font-size: 1.5rem;
    position: absolute;
    right: 18px;
    cursor: pointer;
}


.developer__info{
    width: 30vw;
}

.header__nav__right{
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__nav__right__item ~ .header__nav__right__item{
    margin-left: 30px;
}

.notification__item{
    display: flex;
    align-items: center;
    padding: 20px;
    cursor: pointer;
}


.notification__item > i {
    margin-right: 20px;
    font-size: 1.5rem;
}

.header__nav_logged_in__user{
    display: flex;
    align-items: center;
}

.header__nav_logged_in__user_img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
}

.header__nav_logged_in__user_img > img{
    width: 100%;
    height: 100%;
}

.header__nav_logged_in__username{
    font-size: 1rem;
    font-weight: 600;
    text-transform: capitalize;
}

.header__nav_logged_in__username.dark_mode_username{
    color: var(--txt-white);
}

@media(max-width: 1280px){
    .toggle__button{
        display: block;
    }
}

@media(max-width: 1280px){

}


@media(max-width: 980px){
    .developer__info{
        display: none;
    }
}

@media(max-width: 600px){
    .header__nav_logged_in__username{
        display: none;
    }
    .header__nav__right__item ~ .header__nav__right__item {
        margin-left: 10px;
    }
}

@media(max-width: 480px){
    .header__nav{
        padding: 15px;
    }
    .toggle__button {
        background-color: transparent;
        font-size: 20px;
    }
    .header__nav__search {
        height: 40px;
        width: 40%;
    }
    .header__nav__search > input {
        padding: 8px 37px 10px 10px;
        font-size: 14px;
    }
    .header__nav__search > i {
        font-size: 1.4rem;
        right: 5px;
    }
    .header__nav_logged_in__user_img{
        width: 40px;
        height: 40px;
    }
    .header__nav_logged_in__username{
        font-size: 0.8;
    }

}



.theme__menu{
    position: fixed;
    top: 0;
    right: -300px;
    width: 300px;
    height: 100vh;
    padding: 20px;
    background-color: var(--main-bg);
    box-shadow: var(--box-shadow);
    transition: right .5s var(--transition-cubic);
    -webkit-transition: right .5s var(--transition-cubic);
    -moz-transition: right .5s var(--transition-cubic);
    -ms-transition: right .5s var(--transition-cubic);
    -o-transition: right .5s var(--transition-cubic);
    z-index: 99;
}

.theme__menu.active{
    right: 0;
}

.theme__menu.theme__menu__dark{
    background-color: var(--main-bg-dark);
}

.theme__menu__close{
    position: absolute;
    top: 17px;
    right: 20px;
    font-size: 1.5rem;
    background-color: transparent;
}

.theme__menu__select{
    margin-top: 40px;
}

.theme__menu__select .mode__list{
    margin-top: 20px;
}

.theme__menu__select .mode__list > li {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-top: 10px;
}

.theme__menu__select .mode__list .mode__list__color {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    margin-right: 15px;
    box-shadow: var(--box-shadow);
}

.theme__menu__select .mode__list .mode__list__color > i {
    transform: scale(0);
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transition: transform 0.5s var(--transition-cubic);
    -webkit-transition: transform 0.5s var(--transition-cubic);
    -moz-transition: transform 0.5s var(--transition-cubic);
    -ms-transition: transform 0.5s var(--transition-cubic);
    -o-transition: transform 0.5s var(--transition-cubic);
}

.theme__menu__select .mode__list .mode__list__color.active > i {
    transform: scale(1);
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
}
.layout{
    color: var(--text-white);
}

.layout__content{
    padding-left: var(--sidebar-width);

}

.layout__content__main{
    height: calc(100vh - 180px);
    overflow: auto;
}

