        padding-left: 0;
    }
}
.footer__area{
    height: var(--footer-height);
    padding: 30px;
    box-shadow: 6px 3px 6px #fff;
}
.footer__area .footer__right,
.footer__area .footer__left{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
}

.footer__area .footer__right h5{
    margin-right: 20px;
}

.footer__area .footer__right i {
    padding: 5px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;

}
.footer__area .footer__right i:hover{
    background-color: #838c8b;
    color: #fff;
}
.footer__area.footer__dark{
    background-color: var(--main-bg-dark);
    color: var(--taxt-white);
}

@media(max-width: 1280px){
    .footer__area .footer__right,
    .footer__area .footer__left{
        font-size: 1rem;
    }
    .footer__area .footer__right{
        margin-top: 10px;
    }
}

@media(max-width: 480px){
    .footer__area{
        padding: 10px 30px;
    }
}
.theme-mode-light {
    --main-bg: var(--main-bg-light);
    --second-bg: var(--second-bg-light);
    --txt-color: var(--txt-color-light);
    --box-shadow: var(--box-shadow-light);
}

.theme-mode-dark {
    --main-bg: var(--main-bg-dark);
    --second-bg: var(--second-bg-dark);
    --txt-color: var(--txt-color-dark);
    --box-shadow: var(--box-shadow-dark);
    color: #fff;
}

.theme-color-blue {
    --main-color: var(--main-color-blue);
    --second-color: var(--second-color-blue);
}

.theme-color-red {
    --main-color: var(--main-color-red);
    --second-color: var(--second-color-red);
}

.theme-color-cyan {
    --main-color: var(--main-color-cyan);
    --second-color: var(--second-color-cyan);
}

.theme-color-green {
    --main-color: var(--main-color-green);
    --second-color: var(--second-color-green);
}

.theme-color-orange {
    --main-color: var(--main-color-orange);
    --second-color: var(--second-color-orange);
}
